<template lang="pug">    
    .profile-wrapper
        .form__title Мой профиль
        form.form__profile
          label.label__photo-upload
            input(name='add__photo' type='file' @change="handlePhoto").user__form-file-input 
            .user__form-pic(:class="{filled: renderedPhoto.length}" :style="{backgroundImage: `url(${renderedPhoto})`}")
              .user__change-photo Изменить фото
          .user__info
            .form__row
              label.user__info-label Имя
                input(type='text' :value='Жанаргүл' size='16' placeholder='Имя').user__name
              label.user__info-label Фамилия
                input(type='text' :value='Кашувай' size='16' placeholder='Фамилия').user__lastname
            .form__row 
              label.user__info-label Пароль
                input(type="password" :value='user.password' size='16' placeholder='Пароль').user__password
              label.user__info-label Повторите пароль
                input(type="password" size='16').user__password
            .form__row  
              .user__info-label Сертификаты
                ul.certificates__list
                  li.certificates__item(v-for='certificate in user.certificates')
                    certificateItem(
                      :certificate='certificate'
                    )
              label.user__info-label Добавить сертификат
                addCertificateForm
            .form__row
              label.user__info-label Образование
                input(type='text' :value='user.education' size='16' placeholder='Образование').user__education
              label.user__info-label Цитата
                textarea(type='text' :value='user.quote' size='16' placeholder='Цитата').user__education
            .buttons
              button(type='submit').submit__button Сохранить
              button(type='reset' @click.prevent='resetButton').reset__button Отмена

</template>
<script>
export default {
  components: {
    certificateItem: () => import('../../teacher/profile/certificate-item'),
    addCertificateForm: () => import('../../teacher/profile/add-certificate-form')
  },
  data () {
    return {
      renderedPhoto: '',
      user:{
        name: 'Динара',
        lastname: 'Жусупова',
        education: 'высшее',
        quote: '2 b or not 2 b',
        certificates: [
          {
            title: 'Сертификат #1',
            file: '/path_to_certificate/doc_name'
          },
          {
            title: 'Сертификат #2',
            file: '/path_to_certificate/doc_name'
          },
          {
            title: 'Сертификат #3',
            file: '/path_to_certificate/doc_name'
          },
          {
            title: 'Сертификат #4',
            file: '/path_to_certificate/doc_name'
          }
        ],
        password: 'qwerty', 
        photo: ''
      }
    }
  },
  methods: {
    renderImageFile(file) {
      const reader = new FileReader()
      try {
        reader.readAsDataURL(file)
        reader.onloadend=()=>{
          this.renderedPhoto = reader.result
        }
      } catch (err) {
        throw new Error('Error read file')
      } 
    },
    handlePhoto(e) {
      console.log('from handle')
      const file = e.target.files[0]
      this.renderImageFile(file)
    },
    resetButton() {
      this.user.name = ''
      this.user.lastname= ''
      this.user.school = ''
      this.user.class = 0
      this.user.password = '' 
      this.renderedPhoto = ''
      this.user.quote = ''
      this.user.education = ''
    }
  }
};
</script>
